import { useEffect, useState } from 'react';
import { Grid, Box, Typography, CircularProgress, Alert } from '@mui/material';
import SimpleStockChart from './SimpleStockChart';
import FinanceService from '../../services/FinanceService';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import { useUser } from "../../context/UserContext";

const StockGrid = ({ themeMode, language }) => {
  const { userState } = useUser();

  const [stockData, setStockData] = useState({
    ASX200: { data: null, error: null },
    SSE: { data: null, error: null },
    SP500: { data: null, error: null },
    FTSE: { data: null, error: null },
  });

  const tickers = {
    ASX200: '^AXJO',
    SSE: '000001.SS',
    SP500: '^GSPC',
    FTSE: '^FTSE',
  };

  useEffect(() => {
    const fetchStockData = async () => {
      // 如果 access 为空，则不执行后续操作
      if (!userState.access) {
        return;
      }
      Object.keys(tickers).forEach(async (key) => {
        try {
          const data = await FinanceService.get_stock_history_data(tickers[key], userState.access);
          setStockData((prevData) => ({
            ...prevData,
            [key]: { data, error: null },
          }));
        } catch (error) {
          console.error(`Failed to fetch data for ${key}:`, error);
          setStockData((prevData) => ({
            ...prevData,
            [key]: { data: null, error: 'Failed to load data' },
          }));
        }
      });
    };

    fetchStockData();
  }, [userState.access]);

  const calculateChange = (data) => {
    if (!data || data.history.length < 2) return null;
    const lastClose = data.history[data.history.length - 1].Close;
    const secondLastClose = data.history[data.history.length - 2].Close;
    const change = lastClose - secondLastClose;
    const percentageChange = (change / secondLastClose) * 100;

    return { change, percentageChange };
  };

  const renderStockHeader = (title, data) => {
    const changeData = calculateChange(data);
    return (
      <Typography
        variant="h6"
        sx={{
          fontWeight: 700,
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: themeMode === 'dark' ? 'white' : 'black',
        }}
      >
        {title}
        {changeData && (
          <Typography
            variant="subtitle1"
            component="span"
            sx={{
              marginLeft: 1,
              fontWeight: 600,
              color: changeData.change >= 0
                ? language === 'cn' ? '#ef5350' : '#26a69a' // Red : Green for 'cn'
                : language === 'cn' ? '#26a69a' : '#ef5350', // Green : Red for 'cn'
            }}
          >
            {changeData.change >= 0 ? '+' : ''}
            {changeData.change.toFixed(2)} / 
            {changeData.change >= 0 ? ' +' : ' '}
            {changeData.percentageChange.toFixed(2)}%
          </Typography>
        )}
      </Typography>
    );
  };

  const stockItems = [
    { title: 'ASX 200', key: 'ASX200' },
    { title: 'SSE (Shanghai)', key: 'SSE' },
    { title: 'S&P 500', key: 'SP500' },
    { title: 'FTSE', key: 'FTSE' },
  ];

  return (
    <Grid container spacing={2} sx={{ height: '100%' }}>
      {stockItems.map((item, index) => {
        const { data, error } = stockData[item.key];
        return (
          <Grid key={index} item xs={12} sm={6} sx={{ height: { xs: '25%', sm: '50%' } }}>
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                height: '100%',
                border: '1px solid #ccc',
                borderRadius: 2,
              }}
            >
              {renderStockHeader(item.title, data)}
              <Box
                sx={{
                  width: '100%',
                  height: '250px',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                {error ? (
                    <WarningAmberIcon
                    sx={{
                        color: 'error.main',
                        fontSize: 48, // 设置图标大小
                    }}
                    />
                ) : data ? (
                  <SimpleStockChart data={data} />
                ) : (
                  <CircularProgress color="primary" />
                )}
              </Box>
            </Box>
          </Grid>
        );
      })}
    </Grid>
  );
};

export default StockGrid;